import React, { useRef } from "react";
import { useLoader, useFrame } from "@react-three/fiber";
import { GLTFLoader } from "three/examples/jsm/loaders/GLTFLoader";
import * as THREE from "three";

const Fondo = ({ sentido = -1, vD }) => {
  const gltf = useLoader(GLTFLoader, "/modelos/tableroShow.gltf");
  const tablero = useRef();

  const vec = new THREE.Vector3();
  useFrame(({ camera }) => {
    tablero.current.rotation.y += sentido * 0.002;
    camera.position.lerp(vec.set(vD.x, vD.y, vD.z), 0.05);
  });

  return <primitive scale={[15, 15, 15]} object={gltf.scene} ref={tablero} />;
};

export default Fondo;
