import React, { useEffect, useRef, useState } from "react";
import { Button, Col, Container, Form, Row, Table } from "react-bootstrap";

const GamePrep = ({
  hPartida,
  idJugador,
  hMensajes,
  hSelectFicha,
  hVDestino,
  sendAction,
}) => {
  const [partida, setPartida] = hPartida;
  const [mensajes, setMensajes] = hMensajes;
  const [selectFicha, setSelectFicha] = hSelectFicha;
  const [vDestino, setVDestino] = hVDestino;

  const msjRef = useRef();
  const mensajesRef = useRef();
  function enviarTexto(e) {
    e.preventDefault();
    e.stopPropagation();
    if (!msjRef.current.value) return;
    sendAction("message", msjRef.current.value);
    msjRef.current.value = "";
    msjRef.current.focus();
  }
  useEffect(() => {
    mensajesRef.current.scrollTop = mensajesRef.current.scrollHeight;
  }, [mensajes]);

  function handleConfigFicha(e) {
    setSelectFicha(true);
    setVDestino({ x: 0, y: 6, z: 12 });
    console.log("ficha seleccionada");
  }
  return (
    <Container style={{ width: "500px" }}>
      <h2>
        <Form.Label style={{ color: "aqua" }}> {partida.nombre} </Form.Label>
      </h2>
      <Container
        style={{
          height: 258,
          padding: 0,
          backgroundColor: "rgba(0, 0, 0, 0.5)",
          marginBottom: 20,
          border: "solid 1px white",
        }}
      >
        <Table variant="dark" hover size="sm">
          <thead>
            <tr style={{ fontSize: 14 }}>
              <th style={{ backgroundColor: "black" }}>Nombre</th>
              <th style={{ backgroundColor: "black" }}>Ficha</th>
              <th style={{ backgroundColor: "black" }}>Listo</th>
            </tr>
          </thead>
          <tbody>
            {partida.jugadores.map(function (j) {
              return (
                <tr
                  key={j.id}
                  className={j.id == idJugador ? "table-secondary" : ""}
                  style={{
                    verticalAlign: "middle",
                    fontSize: 14,
                  }}
                >
                  <td>{j.nombre}</td>
                  <td>
                    {j.id == idJugador ? (
                      <Button size="sm" onClick={handleConfigFicha}>
                        {j.ficha}
                      </Button>
                    ) : (
                      <div style={{ paddingBlock: "4px" }}>{j.ficha}</div>
                    )}
                  </td>
                  <td>
                    <Form.Check value="" disabled={j.id != idJugador} />
                  </td>
                </tr>
              );
            })}
          </tbody>
        </Table>
      </Container>
      <Form.Control
        as="textarea"
        rows={4}
        readOnly
        value={mensajes}
        ref={mensajesRef}
        size="sm"
      />
      <Form onSubmit={enviarTexto}>
        <Row className="mt-4">
          <Col xs={9}>
            <Form.Control
              type="text"
              placeholder="Escriba un mensaje..."
              ref={msjRef}
            />
          </Col>
          <Col sm xs={2}>
            <Button variant="primary" type="submit">
              Enviar
            </Button>
          </Col>
        </Row>
      </Form>
    </Container>
  );
};

export default GamePrep;
