import React, { useState, useRef } from "react";
import Login from "./Login";
import useLocalStorage from "../hooks/useLocalStorage";
import GameSettings from "./GameSettings";
import { Canvas } from "@react-three/fiber";
import Fondo from "./Fondo";

function App() {
  const [username, setUsername] = useLocalStorage("username", "");
  const [token, setToken] = useLocalStorage("token", "");
  const [mensajeErr, setMensajeErr] = useState();
  const [vDestino, setVDestino] = useState({ x: 0, y: 40, z: 80 });
  function handleLoginSubmit(nombre) {
    setUsername(nombre);
  }
  return (
    <div style={{ width: "100%", height: "100%", position: "relative" }}>
      <div
        className="container1"
        style={{
          width: "100%",
          position: "absolute",
          zIndex: 1,
        }}
      >
        {username ? (
          <GameSettings
            hUsername={[username, setUsername]}
            hToken={[token, setToken]}
            hMensajeErr={[mensajeErr, setMensajeErr]}
            hVDestino={[vDestino, setVDestino]}
          />
        ) : (
          <Login
            onLoginSubmit={handleLoginSubmit}
            hMensajeErr={[mensajeErr, setMensajeErr]}
          />
        )}
      </div>
      <div
        style={{
          width: "100vw",
          height: "100vh",
          backgroundColor: "black",
        }}
      >
        <Canvas
          camera={{
            fov: 75,
            near: 0.1,
            far: 1000,
            position: [0, 40, 80],
          }}
        >
          <ambientLight color="white" intensity={0.7} />
          <Fondo sentido={username ? 1 : -1} vD={vDestino} />
        </Canvas>
      </div>
    </div>
  );
}

export default App;
