import React from "react";
import { Button, Col, Row } from "react-bootstrap";
import { Canvas } from "@react-three/fiber";

const SelectFicha = ({ hShow, hVDestino }) => {
  const [vDestino, setVDestino] = hVDestino;
  const [show, setShow] = hShow;

  function handleSeleccionar() {
    setShow(false);
    setVDestino({ x: 0, y: 40, z: 80 });
  }
  return (
    <div>
      <Row className="align-items-center">
        <Col>
          <Button>{"<"}</Button>
        </Col>
        <Col>
          <div style={{ width: 180, height: 400 }}>
            <Canvas
              camera={{ fov: 75, near: 0.1, far: 1000, position: [0, 40, 80] }}
            >
              <ambientLight color="white" intensity={0.7} />
              <mesh>
                <boxGeometry args={[4, 4, 4]} />
                <meshBasicMaterial color="red" />
              </mesh>
            </Canvas>
          </div>
        </Col>
        <Col>
          <Button>{">"}</Button>
        </Col>
      </Row>
      <Row>
        <Col>
          <Button onClick={handleSeleccionar}>Seleccionar</Button>
        </Col>
      </Row>
    </div>
  );
};

export default SelectFicha;
