import React, { useEffect, useState } from "react";
import SelectGame from "./SelectGame";
import { Col, Row, Image, Container } from "react-bootstrap";
import Exit from "./Exit";
import useWebSocket, { ReadyState } from "react-use-websocket";
import Alerta from "./Alerta";
import GamePrep from "./GamePrep";
import SelectFicha from "./SelectFicha";

const GameSettings = ({ hUsername, hToken, hMensajeErr, hVDestino }) => {
  const [token, setToken] = hToken;
  const [username, setUsername] = hUsername;
  const [partidas, setPartidas] = useState();
  const [partida, setPartida] = useState();
  const [idJugador, setIdJugador] = useState();
  const [mensajeErr, setMensajeErr] = hMensajeErr;
  const [mensajes, setMensajes] = useState();
  const [selectFicha, setSelectFicha] = useState();
  const [vDestino, setVDestino] = hVDestino;

  const loc = window.location;
  const wsurlPRD = `ws${loc.protocol == "http:" ? "" : "s"}://${loc.host}`;
  const wsurlDEV = `ws://${loc.hostname}:3000`;
  const wsurl = import.meta.env.DEV ? wsurlDEV : wsurlPRD;

  const { sendJsonMessage, lastJsonMessage, readyState } = useWebSocket(wsurl, {
    queryParams: { token: token, username: username },
    onError: handleError,
    share: false,
    //shouldReconnect: (closeEvent) => true,
  });

  useEffect(() => {
    if (readyState === ReadyState.OPEN) {
      console.log("conexión abierta!");
      setMensajes("");
    } else if (readyState === ReadyState.CLOSED) {
      limpiarConexion();
      console.log("conexión cerrada!");
    }
  }, [readyState]);

  useEffect(() => {
    if (lastJsonMessage) {
      let rsp = lastJsonMessage;
      console.log(`El mensaje recibido es: ${JSON.stringify(rsp)}`);
      switch (rsp.type) {
        case "loggedin":
          let j = rsp.content;
          setToken(j.token);
          setIdJugador(j.id);
          break;
        case "games":
          setPartidas(rsp.content);
          break;
        case "game":
          setPartida(rsp.content.partida);
          if (rsp.content.msj != "")
            setMensajes(`${mensajes ? mensajes : ""}${rsp.content.msj}\n`);
          break;
        case "error":
          console.log(rsp.content);
          setMensajeErr(rsp.content);
          break;
        case "message":
          if (rsp.content != "")
            setMensajes(`${mensajes ? mensajes : ""}${rsp.content}\n`);
          break;
      }
    }
  }, [lastJsonMessage]);

  function handleError(evt) {
    console.log("Se ha detectado un error en la conexión");
  }

  function handleExit() {
    sendAction("logout");
  }

  function limpiarConexion() {
    setToken("");
    setUsername("");
    setIdJugador(0);
  }

  function sendAction(tipo, contenido) {
    const msg = {
      type: tipo,
      content: contenido,
      token: token,
      date: Date.now(),
    };
    sendJsonMessage(msg);
  }

  return (
    <>
      <Row className="mt-4" style={{ height: 32 }}>
        <Exit Nombre={username} onExit={handleExit} />
      </Row>
      <Container style={{ maxWidth: "500px" }}>
        <div className="logo">
          <img src="imagenes/logo_64.png" loading="lazy" alt="" />
        </div>
        <Row>
          <Col className="d-flex justify-content-center">
            {token ? (
              partida ? (
                selectFicha ? (
                  <SelectFicha
                    hShow={[selectFicha, setSelectFicha]}
                    hVDestino={[vDestino, setVDestino]}
                  />
                ) : (
                  <GamePrep
                    hPartida={[partida, setPartida]}
                    idJugador={idJugador}
                    hMensajes={[mensajes, setMensajes]}
                    hSelectFicha={[selectFicha, setSelectFicha]}
                    hVDestino={[vDestino, setVDestino]}
                    sendAction={sendAction}
                  />
                )
              ) : (
                <SelectGame
                  partidaNomDef={"Partida de " + username}
                  hPartidas={[partidas, setPartidas]}
                  sendAction={sendAction}
                />
              )
            ) : (
              <div style={{ height: 400 }}>Cargando...</div>
            )}
          </Col>
        </Row>
        <br />
        <Alerta hMensajeErr={[mensajeErr, setMensajeErr]} />
      </Container>
    </>
  );
};

export default GameSettings;
